import React from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { Text, Flex, Link, LinkExternal } from 'uikit'
import { QuoteToken, Swap } from 'config/constants/types'
import contracts from 'config/constants/contracts'
import { FarmWithStakedValue } from 'state/types'

export interface ExpandableSectionProps {
  farm: FarmWithStakedValue
}

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

const Wrapper = styled.div`
  margin-top: 24px;
`

const StyledLinkExternal = styled(LinkExternal)`
  text-decoration: none;
  font-weight: normal;
  color: ${({ theme }) => theme.colors.text};
  display: flex;
  align-items: center;

  svg {
    padding-left: 4px;
    height: 18px;
    width: auto;
    fill: ${({ theme }) => theme.colors.primary};
  }
`

const getSwapUrl = (swap: Swap) => {
  return swap === Swap.SPOOKY ? 'https://spookyswap.finance/' : 'https://swap.spiritswap.finance/#/'
}

const getFullSwapUrl = ({ tokenSymbol, quoteTokenSymbol, isTokenOnly, swap }: FarmWithStakedValue) => {
  const firstPart = quoteTokenSymbol === QuoteToken.WFTM ? 'ETH' : contracts[quoteTokenSymbol][CHAIN_ID]

  if (isTokenOnly) return `${getSwapUrl(swap)}swap?outputCurrency=${firstPart}`

  const secondPart = contracts[tokenSymbol][CHAIN_ID]
  return `${getSwapUrl(swap)}add/${firstPart}/${secondPart}`
}

const getUrls = (a) => {
  const fullUrl = getFullSwapUrl(a)

  const bscScanAddress = a.isTokenOnly
    ? `https://ftmscan.com/token/${contracts[a.tokenSymbol][CHAIN_ID]}`
    : `https://ftmscan.com/token/${a.lpAddresses}`

  return [fullUrl, bscScanAddress]
}

const DetailsSection: React.FC<ExpandableSectionProps> = ({ farm }) => {
  const [liquidityUrlPathParts, bscScanAddress] = getUrls(farm)
  const { userData, total, totalValue, isTokenOnly } = farm
  const totalValueFormated = totalValue
    ? `$${Number(totalValue).toLocaleString(undefined, { maximumFractionDigits: 0 })}`
    : '-'

  const balance = new BigNumber(userData?.stakedBalance).div(new BigNumber(10).pow(18))
  const totalBalance = isTokenOnly ? new BigNumber(total) : new BigNumber(total).div(new BigNumber(10).pow(18))
  const share = balance.div(totalBalance)
  const liquidity = `$${Number(share.multipliedBy(totalValue)).toLocaleString(undefined, { maximumFractionDigits: 0 })}`

  return (
    <Wrapper>
      <Flex justifyContent="space-between">
        <Text>{farm.swap || Swap.SPIRIT}</Text>
        <StyledLinkExternal href={liquidityUrlPathParts}>{farm.lpSymbol}</StyledLinkExternal>
      </Flex>
      <Flex justifyContent="space-between">
        <Text>Your Liquidity:</Text>
        <Text>{liquidity}</Text>
      </Flex>
      <Flex justifyContent="space-between">
        <Text>Your Share:</Text>
        <Text>{share.times(100).toFixed(4)}%</Text>
      </Flex>
      <Flex justifyContent="space-between">
        <Text>Total Liquidity:</Text>
        <Text>{totalValueFormated}</Text>
      </Flex>
      <Flex justifyContent="flex-start">
        <Link external href={bscScanAddress} bold={false}>
          View on FtmScan
        </Link>
      </Flex>
    </Wrapper>
  )
}

export default DetailsSection
