import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import { Button, Flex, Heading } from 'uikit'
import useHarvest from 'hooks/useHarvest'
import { getBalanceBig, getBalanceNumber } from 'utils/formatBalance'
import styled from 'styled-components'
import { Farm } from 'state/types'

interface FarmCardActionsProps {
  earnings?: BigNumber
  farm: Farm
}

const BalanceAndCompound = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
`

const HarvestAction: React.FC<FarmCardActionsProps> = ({ earnings, farm }) => {
  const [pendingTx, setPendingTx] = useState(false)
  const { onReward } = useHarvest(farm)

  const rawEarningsBalance = getBalanceBig(earnings)
  const rawEarningsBalanceNumber = getBalanceNumber(earnings)
  
  return (
    <Flex mb="8px" justifyContent="space-between" alignItems="center">
      <Heading color={rawEarningsBalanceNumber === 0 ? 'textDisabled' : 'text'}>{rawEarningsBalance.toFixed(5)}</Heading>
      <BalanceAndCompound>
        <Button
          disabled={rawEarningsBalanceNumber === 0 || pendingTx}
          onClick={async () => {
            setPendingTx(true)
            await onReward()
            setPendingTx(false)
          }}
        >
          Harvest
        </Button>
      </BalanceAndCompound>
    </Flex>
  )
}

export default HarvestAction
